@tailwind components;
@tailwind utilities;
.skip-to-content-link {
  background: #e77e23;
  height: 40px;
  left: 50%;
  padding: 8px;
  position: absolute;
  transform: translateY(-100%);
  transition: transform 0.3s;
  z-index: 99;
  visibility: hidden; }

.skip-to-content-link:focus {
  transform: translateY(0%);
  visibility: visible; }

.quote-nav {
  top: 50%; }

.speaker img {
  width: 100%;
  height: auto; }

.speaker-overlay {
  background: transparent;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 60%, #d42a46 100%, #a3031e 100%); }
